<template>
  <div>
    <vue-confirm-dialog></vue-confirm-dialog>

    <div class="row">
      <div class="col-md-12" ref="table">
        <v-card>
          <v-card-title>
            Records

            <v-spacer></v-spacer>
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Search"
              single-line
              hide-details
            ></v-text-field>

            <v-spacer></v-spacer>
          </v-card-title>

          <v-data-table
            
            :headers="headers"
            :items="records"
            :search="search"
            item-key="id"
          >
            <template v-slot:item.status_id="{ item }">
              <td class="text-right">
                <span
                  v-if="item.status_id == 1"
                  class="label label-lg label-light-warning label-inline"
                >
                  Pendding
                </span>
                <span
                  v-if="item.status_id == 2"
                  class="label label-lg label-light-success label-inline"
                >
                  Finished
                </span>
              </td>
            </template>
            <template v-slot:item.actions="{ item }">
              <b-button
                @click="finish(item)"
                title="Mark as done"
                class="mx-1"
                pill
              >
                <v-icon small>fa fa-check</v-icon></b-button
              >

              <b-button
                title="Assign to an employee"
                @click="assign(item)"
                v-scroll-to="'#formData'"
                class="mx-1"
                v-if="role_id == 2"
                pill
              >
                <v-icon small>fa fa-user-plus</v-icon></b-button
              >
            </template>
          </v-data-table>
        </v-card>
      </div>
    </div>
    <hr />
    <div class="row">
      <div id="formData" v-if="role_id == 2" class="col-md-12">
        <v-card>
          <v-card-title>Fill in Form Data</v-card-title>
          <v-card-text>
            <validation-observer ref="observer" v-slot="{ handleSubmit }">
              <b-form
                enctype="multipart/form-data"
                @submit.stop.prevent="handleSubmit(saveEmp)"
              >
                <validation-provider
                  name="Request ID"
                  rules="required"
                  v-slot="validationContext"
                >
                  <b-form-group label="Request ID">
                    <b-form-input
                      readonly
                      v-model="form.req_id"
                      :state="getValidationState(validationContext)"
                      aria-describedby="input-1-live-feedback"
                    ></b-form-input>

                    <b-form-invalid-feedback id="input-1-live-feedback">
                      {{ validationContext.errors[0] }}
                    </b-form-invalid-feedback>
                  </b-form-group>
                </validation-provider>

                <validation-provider
                  name="Name"
                  rules="required"
                  v-slot="validationContext"
                >
                  <b-form-group label="Employee Name">
                    <b-form-select
                      v-model="form.emp_id"
                      :options="staff"
                      class="mb-3"
                      :state="getValidationState(validationContext)"
                      aria-describedby="name"
                    >
                      <!-- This slot appears above the options from 'options' prop -->
                      <template v-slot:first>
                        <b-form-select-option :value="null"
                          >-- Please select an option --</b-form-select-option
                        >
                      </template>
                    </b-form-select>
                    <b-form-invalid-feedback id="name">
                      {{ validationContext.errors[0] }}
                    </b-form-invalid-feedback>
                  </b-form-group>
                </validation-provider>

                <b-button type="submit" variant="success">Save</b-button>
                <b-button class="ml-2" variant="warning" @click="resetForm()"
                  >Reset</b-button
                >
              </b-form>
            </validation-observer>
          </v-card-text>
        </v-card>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";

const VueScrollTo = VueScrollTo;
import { SET_BREADCRUMB } from "@/core/services/store/breadcrumbs.module";

export default {
  mounted() {
   this.$store.dispatch(SET_BREADCRUMB, [
      { title: "Requests"},
      { title: " طلبات التواصل ",route: "landContact" }
    ]);
    this.$ApiService
      .get(`/getUser`)
      .then((response) => {
        this.role_id = response.data.data.role_id;
      })
      .catch((err) => {});
  },
  data() {
    return {
      role_id: 1,
      records: [],
      staff: [],
      edit: false,
      record_id: "",
      form: {
        emp_id: null,
        req_id: null,
      },
      search: "",
      headers: [
        {
          text: "ID",
          align: "start",
          sortable: false,
          value: "id",
        },

        { text: "Plan", value: "plan.name" },
        { text: "Plot NO", value: "plot.plot_no" },
        { text: "Name", value: "title" },
        { text: "Message", value: "description" },
        { text: "User", value: "user.name" },
        { text: "Assigned to", value: "employee.employee_name" },
        { text: "Date", value: "created_at" },
        { text: "Status", value: "status_id", sortable: false },
        { text: "Actions", value: "actions", sortable: false },
      ],
    };
  },
  getStatus(status_id) {
    return status_id == 1 ? "Pendding" : "Finished";
  },
  created() {
    this.fetchRecords();
    this.fetchStaff();
  },
  methods: {
    getValidationState({ dirty, validated, valid = null }) {
      return dirty || validated ? valid : null;
    },

    resetForm() {
      this.form = {
        req_id: null,
        emp_id: null,
      };

      this.$nextTick(() => {
        this.$refs.observer.reset();
      });
    },
    fetchRecords() {
      this.$ApiService
        .get("/LandContact")
        .then((response) => {
          //console.log(response);
          this.records = response.data.data;
        })
        .catch((err) => {
          Vue.$toast.error(`${err}`);
          console.log(err);
        });
    },
    fetchStaff() {
      this.$ApiService
        .get("/Staff")
        .then((response) => {
          this.staff = response.data.data;
        })
        .catch((err) => {
          Vue.$toast.error(`${err}`);
          console.log(err);
        });
    },
    deleteItem(item) {
      this.$confirm({
        title: "Confirmation",
        message: `Are you sure you want to proceed?`,
        button: {
          no: "No",
          yes: "Yes",
        },
        callback: (confirm) => {
          if (confirm) {
            this.$ApiService
              .delete(`/Feedback/${item.id}`)
              .then((response) => {
                if (response.data.error == 1) {
                  this.$refs.table.scrollIntoView();
                  this.fetchRecords();
                  Vue.$toast.success(`${response.data.result}`);
                } else {
                  Vue.$toast.error(`${response.data.result}`);
                }
              })
              .catch((err) => Vue.$toast.error(`${err}`));
          }
        },
      });
    },
    assign(item) {
      this.form.req_id = item.id;
    },
    saveEmp() {
      this.$ApiService
        .put(`/LandContactAssign`, this.form)
        .then((response) => {
          if (response.data.error == 1) {
            this.resetForm();
            this.record_id = "";
            this.$refs.table.scrollIntoView();
            this.fetchRecords();
            Vue.$toast.success(`${response.data.result}`);
          } else {
            Vue.$toast.error(`${response.data.result}`);
          }
        })
        .catch((err) => Vue.$toast.error(`${err}`));
    },
    finish(item) {
      this.$confirm({
        title: "Confirmation",
        message: `Are you sure you want to mark this as done ?`,
        button: {
          no: "No",
          yes: "Yes",
        },
        callback: (confirm) => {
          if (confirm) {
            this.$ApiService
              .put(`/landContactFinish/${item.id}`)
              .then((response) => {
                if (response.data.error == 1) {
                  this.$refs.table.scrollIntoView();
                  this.fetchRecords();
                  Vue.$toast.success(`${response.data.result}`);
                } else {
                  Vue.$toast.error(`${response.data.result}`);
                }
              })
              .catch((err) => {
                Vue.$toast.error("Some error happened, please try again!");
                console.log(err);
              });
          }
        },
      });
    },
  }, // end of methods
}; //end of class
</script>
